<script>
import AFormItem from 'ant-design-vue/es/form/FormItem'
import DatePicker from 'ant-design-vue/es/date-picker'

import Utils from '../utils'

export default {
  name: 'BaseYearPicker',
  inheritAttrs: false,
  data () {
    return {
      format: 'YYYY',
      mode: 'year',
      open: false
    }
  },
  methods: {
    /**
     * @description 绑定作用域
     * @param {Object} obj
     * @returns {Object}
     */
    handleScope (obj) {
      const scope = { ...obj }
      const names = Object.keys(obj)
      for (const name of names) {
        if (Utils.isFunction(scope[name])) {
          scope[name] = scope[name].bind(this)
        }
      }
      return scope
    },

    /**
     * @description 打开年份面板
     * @param {Date} value
     * @returns {undefined}
     */
    handlePanelChange (value) {
      this.$attrs.form.setFieldsValue({ [this.$attrs.field]: value || null })
      this.open = !!false
    },

    /**
     * @description 关闭年份面板
     * @returns {undefined}
     */
    handleOpenChange (state) {
      this.open = !!state
    },

    /**
     * @description 清理年份值
     * @returns {undefined}
     */
    handleChange () {
      this.$attrs.form.setFieldsValue({ year: null })
    }
  },
  render () {
    const open = this.open
    const mode = this.mode
    const format = this.format
    const label = this.$attrs.label
    const field = this.$attrs.field
    const binds = this.$attrs.binds
    const events = this.$attrs.events
    const decorator = this.$attrs.decorator
    const classAttrs = this.$attrs.binds.class
    const styleAttrs = this.$attrs.binds.style
    const handleScope = this.handleScope
    const handlePanelChange = this.handlePanelChange
    const handleOpenChange = this.handleOpenChange
    const handleChange = this.handleChange

    return (
      <AFormItem label={label}>
        <DatePicker
          ref="component"
          v-decorator={[field, decorator]}
          attrs={handleScope(binds)}
          on={handleScope(events)}
          class={classAttrs}
          style={styleAttrs}
          format={format}
          mode={mode}
          open={open}
          onPanelChange={handlePanelChange}
          onOpenChange={handleOpenChange}
          onChange={handleChange}
        />
      </AFormItem>
    )
  }
}
</script>

<style lang="less">
[off-disabled] {
  .ant-input[disabled],
  .ant-input-disabled {
    color: rgba(0, 0, 0, 0.65);
    background-color: #ffffff;
    cursor: default;
    opacity: 1;
    & + .ant-calendar-picker-icon {
      cursor: default;
    }
  }
}
</style>
